.appBody{
   position: fixed;
   width: 460px;
   height: 300px;
   left: 40%;
   top: 20%;
   z-index: 10;
   background-color: azure;
   padding: 10px 10px 10px 10px;
}
.title{
    font-size:30px;
    margin-right: 80px;
    color:darkturquoise;
}
.loginPage{
    width:350px;
    height:100px;
    margin-left:15%;
    margin-top: 50px;

}
.formContent{
    margin-top:20px;
}
.content{
    width: 230px;
    height:20px;
    font-size: 13px;
    outline-style: none ;
    border: 1px solid #ccc;
    border-radius: 3px;
    padding:5px;
    margin-left: 20px;
}
.rightBtn{
    display: inline;
}
.doLogin{
    width: 80px;
    height: 30px;
    font-size: 18px;
    outline-style: none ;
    border: 1px solid #ccc;
    float: left;
    border-radius: 6px;
    margin-top: 18px;
    margin-left: 25px;
    text-align: center;
    background-color: darkturquoise;
}
.doRegister{
    width: 80px;
    height: 30px;
    font-size: 18px;
    outline-style: none ;
    border: 1px solid #ccc;
    border-radius: 6px;
    margin-top: 18px;
    margin-left:75px;
    text-align: center;
    background-color: darkturquoise;
}
span{
    color: red;
    height: 10px;
    font-size: 15px;
}
label{
    margin-left: 4px;
    color: red;
    font-size: 10px;
}